import React, { useState } from 'react'
import Modal from 'react-modal'
import Item from '../ShopItems/Item/Item'

let ItemInfoModal = (props) => {
	const [modal, setModal] = useState(false)

	let openModal = () => {
		setModal(true)
	}

	let closeModal = () => {
		setModal(false)
	}

	const customStyles = {
		content: {
			top: '20%',
			left: '40%',
			width: '60%',
            height: '55%',
			transform: 'translate(-40%, -10%)'
		},
	}

	return (
		<div>
			<div >
				<button onClick={openModal}>More info</button>
			</div>

			<Modal style={customStyles} isOpen={modal} onRequestClose={closeModal} ariaHideApp={false}>
				<Item src={props.src} name={props.name} count={props.count} size={props.size} price={props.price} weight={props.weight} comments={props.comments}/>
			</Modal>
			
		</div>
	)
}

export default ItemInfoModal
