import React, { useState } from 'react'
import ModalWindow from '../ModalWindow/AddModalWindow'
import s from './ShopItems.module.css'
import ItemInfoModal from '../ModalWindow/ItemInfoModal'

const ShopItems = (props) => {
	const [value, setValue] = useState('')

	let onSubmit = (formData) => {
		props.addNewItem(formData)
	}

	let filteredGoods = props.goods.filter((a) => {
		return a.name.toLocaleLowerCase().includes(value.toLocaleLowerCase())
	})

	return (
		<div>
			<div className={s.position}>
				<div>
					<form>
						<input
							type='text'
							placeholder='Search...'
							onChange={(e) => setValue(e.target.value)}
							className={s.search}
						/>
					</form>
				</div>
				<div className={s.select}>
					<select
						onChange={(e) => {
							props.sortItems(e.target.value)
						}}
					>
						<option value='start_date'>Start Date</option>
						<option value='name'>Name</option>
						<option value='count'>Count</option>
					</select>
				</div>
			</div>
			<div className={s.flex}>
				{filteredGoods.map((i) => (
					<div className={s.flexItm}>
						{props.isAuth ? (
							<img
								onClick={() => {
									props.deleteItem(i.id)
								}}
								className={s.img}
								src='https://findicons.com/files/icons/1262/amora/256/delete.png'
								alt='delete'
							/>
						) : (
							''
						)}
						<img className={s.photo} src={i.src} alt='logo' />
						<div>{i.name} </div>
						<div>{i.price}</div>
						<ItemInfoModal src={i.src} name={i.name} count={i.count} size={i.size} price={i.price} weight={i.weight} comments={i.comments} />
					</div>
				))}
				{props.isAuth ? <ModalWindow onSubmit={onSubmit} /> : ' '}
			</div>
		</div>
	)
}

export default ShopItems
